
import React from 'react';

import { Menu, Dropdown } from 'antd';
import './EditPage.less'


export default class Partition extends React.Component {

    state = {
        visible: false
    }

    handleVisibleChange = (visible) => {
        this.setState({
            visible
        })
    }

    render() {
      const {partitions, generateStoryByBreakdown} = this.props;
      const menu = (
          <Menu>
            {partitions.map(d => 
                <Menu.Item key={d} onClick={() => generateStoryByBreakdown(d)}>
                    {d.replace(/_/g, " ")}
                </Menu.Item>)}
          </Menu>
        );

        return ( 
            <div className={this.state.visible?'edit-partition-container edit-partition-container-choosed':'edit-partition-container'} style={this.props.style}>
                <div className="fs-split" >
                    <i aria-label="icon: plus" className="anticon anticon-plus"><svg style={{ color: '#bec1c3' }} viewBox="64 64 896 896" focusable="false" className="" data-icon="plus" width="1em" height="1em" fill="currentColor" aria-hidden="true"><path d="M482 152h60q8 0 8 8v704q0 8-8 8h-60q-8 0-8-8V160q0-8 8-8z"></path></svg></i>
                </div>
                <Dropdown placement="topCenter" overlay={menu} overlayClassName="edit-partition-choice" onVisibleChange={this.handleVisibleChange} arrow trigger={['click']}>
                    <div className='edit-partition'>
                        <div className='edit-partition-icon'></div>
                        <div>{this.props.children}</div>
                    </div>
                </Dropdown>
                <div className="fs-split" >
                    <i aria-label="icon: plus" className="anticon anticon-plus"><svg style={{ color: '#bec1c3' }} viewBox="64 64 896 896" focusable="false" className="" data-icon="plus" width="1em" height="1em" fill="currentColor" aria-hidden="true"><path d="M482 152h60q8 0 8 8v704q0 8-8 8h-60q-8 0-8-8V160q0-8 8-8z"></path></svg></i>
                </div>
            </div>
        )
    }
}